import { ChangeDetectorRef, Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { VhQuerySales, VhAlgorithm } from 'ionic-vhframeworks';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';


@Component({
  selector: 'app-promotions',
  templateUrl: './promotions.component.html',
  styleUrls: ['./promotions.component.scss']
})
export class PromotionsComponent implements OnInit {
  public promotions: any[] = [];
  tableHeight: any;
  public date: Date[] = [new Date(), new Date()];
  type: any = 'all'
  promotions_type: Array<any> = [
    { value: "all", name: "All" },
    { value: 1, name: "Discount in amount" },
    { value: 2, name: "Discount in percent" },
    { value: 3, name: "Sn phm ng gi" },
    { value: 4, name: "Gim gi tin khi mua nhiu sn phm" },
    { value: 5, name: "Gim phn trm khi mua nhiu sn phm" },
    { value: 6, name: "Tng qu khi mua nhiu sn phm" },
    { value: 7, name: "Tng qu theo tng gi tr n hng" },
  ]
  dataRestore: any = this.router.getCurrentNavigation().extras.state
  constructor(
    private router: Router,
    private vhQuerySales: VhQuerySales,
    private cdRef: ChangeDetectorRef,
    private vhAlgorithm: VhAlgorithm,
    private vhComponent: VhComponent,
  ) { }

  ngOnInit(): void {
    this.date[0].setHours(0, 0, 0, 0)
    this.date[1].setHours(23, 59, 59, 59)
    if (this.dataRestore) {
      this.type = this.dataRestore.type
      this.date = this.dataRestore.date
      // this.dataRestore.check = true => c thay i khi thm xoa sa th get mi d liu
      if (this.dataRestore.check) this.getPromotions_fromTime_toTime(new Date(this.date[0].setHours(0, 0, 0)), new Date(this.date[1].setHours(23, 59, 59, 59)), this.type)
      else this.promotions = this.dataRestore.data
    }
  }

  /**
   * xem khuyn mi
   */
  showPromotions() {
    this.vhComponent.showLoading("").then(() => {
      this.getPromotions_fromTime_toTime(new Date(this.date[0].setHours(0, 0, 0)), new Date(this.date[1].setHours(23, 59, 59, 59)), this.type, true)
    })
  }


  /**
   * get d liu khuyn mi t DB
   */
  getPromotions_fromTime_toTime(fromtime, totime, type, check?) {
    this.vhQuerySales.getPromotions_fromTime_toTime(fromtime, totime, type)
      .then((response) => {
        this.promotions = this.vhAlgorithm.sortVietnamesebyASC(response, 'name').map(item => {
          return {
            ...item,
            render_type: this.renderType(item.type)
          }
        })
        

        if (check) this.vhComponent.hideLoading()
      }, err => {})
  }
  /**
   * bt sk thay i loi khuyn mi
   */
  selectType(event) {
    this.type = event
  }
  /**
   * bt sk thay i thi gian chy CTKM
   */
  changeTime(event) {
    this.date = [new Date(event[0]), new Date(event[1])]
  }
  /** ------------------------- */
  /**
   * router qua trang thm khuyn mi
   */
  addPromotion() {
    let data = {
      data: this.promotions,
      type: this.type,
      date: this.date,
    }
    this.router.navigate(['/sales/dashboard/manage/sales-program/promotions/add'], { state: data });
  }
  /**
   * router qua trang xem chi tit khuyn mi
   */
  goToPromotionsDetail(promotion) {
    let data = {
      promotion: promotion,
      data: this.promotions,
      type: this.type,
      date: this.date,
    }
    this.router.navigate(['/sales/dashboard/manage/sales-program/promotions/detail'], { state: data })
  }
  /**
   * tr v tn loi khuyn mi theo type khuyn mi
   */
  public renderType(type) {
    switch (type) {
      case 1: return 'Discount in amount'
      case 2: return 'Discount in percent'
      case 3: return 'Sn phm ng gi'
      case 4: return 'Gim gi tin khi mua nhiu sn phm'
      case 5: return 'Gim phn trm khi mua nhiu sn phm'
      case 6: return 'Tng qu khi mua nhiu sn phm'
      case 7: return 'Tng qu theo tng gi tr n hng'
    }
  }
  /**
   * cp nht trng status ca khuyn mi
   */
  public updateToggle(_id, value) {
    this.vhQuerySales.updatePromotion(_id, { status: value, _id: _id });
  }
  ngAfterViewChecked() {
    if (document.getElementById("promotion-invoice-today").clientHeight && document.querySelector(".promotion-today-header") && document.querySelector(".ant-table-thead") && document.querySelector(".ant-table-pagination")) {
      this.tableHeight = document.getElementById("promotion-invoice-today").clientHeight - document.querySelector(".promotion-today-header").clientHeight - document.querySelector(".ant-table-thead").clientHeight
        - document.querySelector(".ant-table-pagination").clientHeight - 80 + "px";
    }
    this.cdRef.detectChanges();
  }
  goBack() {
    this.router.navigate(['/sales/dashboard/manage/sales-program']);
  }

  /** hm sort cho cc ct */
  nameCol = false;
  date_startCol = false;
  date_endCol = false;
  render_typeCol = false;
  /** Hm thc hin sp xp theo collection
   *
   * @param colName       // tn ct mun sp xp
   */
  sortTable(colName) {
    switch (colName) {
      case 'name':
        if (this.nameCol) {
          this.promotions = this.vhAlgorithm.sortVietnamesebyASC([...this.promotions], colName);
        } else {
          this.promotions = this.vhAlgorithm.sortVietnamesebyDESC([...this.promotions], colName);
        }
        break;
      case 'date_start':
        if (this.date_startCol) {
          this.promotions = this.vhAlgorithm.sortDatebyASC([...this.promotions], colName);
        } else {
          this.promotions = this.vhAlgorithm.sortDatebyDESC([...this.promotions], colName);
        }
        break;
      case 'date_end':
        if (this.date_endCol) {
          this.promotions = this.vhAlgorithm.sortDatebyASC([...this.promotions], colName);
        } else {
          this.promotions = this.vhAlgorithm.sortDatebyDESC([...this.promotions], colName);
        }
        break;
      case 'render_type':
        if (this.render_typeCol) {
          this.promotions = this.vhAlgorithm.sortVietnamesebyASC([...this.promotions], colName);
        } else {
          this.promotions = this.vhAlgorithm.sortVietnamesebyDESC([...this.promotions], colName);
        }
        break;
    }
  }
}
