import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { VhAlgorithm, VhQuerySales } from 'ionic-vhframeworks';
import { NzModalService } from 'ng-zorro-antd/modal';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { LanguageService } from 'src/app/sales/services/language.service';

@Component({
  selector: 'app-discount-take-gift',
  templateUrl: './discount-take-gift.component.html',
  styleUrls: ['./discount-take-gift.component.scss']
})
export class DiscountTakeGiftComponent implements OnInit {

  @Output() updateProducts = new EventEmitter<any>();
  @Output() gift_length = new EventEmitter<number>();
  searchProductList: boolean = false;
  searchProductGift: boolean = false;
  quantity: number = 1;
  buy = [];
  gift = []
  data = {
    dataBuy: [],
    dataGift: []
  }
  constructor(
    private vhQuerySales: VhQuerySales,
    public vhAlgorithm: VhAlgorithm,
    private lang: LanguageService,
    private vhComponent: VhComponent,
    private modal: NzModalService
  ) { }
  
  
  ngOnInit(): void {

  }
  

  mathRound(value) {
    return Math.round(value)
  }

  /**
   * m drawer chn sp mua
   */
  openSearchProductList() {
    this.searchProductList = true;
  }

  /**
   * m drawer chn sp tng
   */
  openSearchProductGift() {
    this.searchProductGift = true
  }

  
  /**
   * bt sk thay i unit ca sp
   */
  getPrice(item, e) {
    item.price = item.unit_array.find(it => it.unit == e).price;
    item.ratio = item.unit_array.find(it => it.unit == e).ratio
    item.unit = e
  }

  /**
   * bt sk ng drawer chn sp tng
   * @param data 
   */
  closeSearchProductGift(data) {
    if (data) {
      for (let i = 0; i < data.length; i++) {
        let unit_array = [];
        data[i] = {...data[i],unit_array }
        // let unitMain = {
        //   unit: data[i].unit,
        //   ratio: 1,
        //   barcode: data[i].barcode,
        //   price: data[i].price,
        //   price2: data[i].price2,
        //   price_important: data[i].price_important,
        // };
        // data[i]['unit_array'].unshift(unitMain);
        if (data[i].units) {
          data[i]['unit_array'] = data[i]['unit_array'].concat(data[i].units)
        }
        let quantity_discount = 1;
        let price_gift = 0
        data[i] = {...data[i], quantity_discount: quantity_discount, price_gift: price_gift}
        if((this.gift.length + data.length) <= 100) {
          this.gift.unshift(data[i]);
        } else {
          this.modal.error({
            nzTitle: 'S lng sn phm vt qu gii hn',
            nzContent: 'Vui lng to thm khuyn mi khc !'
          });
          break;
        }
      }
    }
    this.gift_length.emit(this.gift.length)
    this.searchProductGift = false;

  }

  /**
   * bt sk ng drawer chn sp mua
   * @param data 
   */
  closeSearchProductList(data) {     
    if (data) {
      for (let i = 0; i < data.length; i++) {
        let unit_array = [];
        data[i] = {...data[i],unit_array }
        // let unitMain = {
        //   unit: data[i].unit,
        //   ratio: 1,
        //   barcode: data[i].barcode,
        //   price: data[i].price,
        //   price2: data[i].price2,
        //   price_important: data[i].price_important,
        // };
        // data[i]['unit_array'].unshift(unitMain);
        if (data[i].units) {
          data[i]['unit_array'] = data[i]['unit_array'].concat(data[i].units)
        }
        let quantity_discount = 1;
        data[i] = {...data[i], quantity_discount: quantity_discount}
        if((this.buy.length + data.length) <= 100) {
          this.buy.unshift(data[i]);
        } else {
          this.modal.error({
            nzTitle: 'S lng sn phm vt qu gii hn',
            nzContent: 'Vui lng to thm khuyn mi khc !'
          });
          break;
        }
      }
    }

    this.searchProductList = false;
  }
  /**
   * bt sk nhn nt lu km , output v component cha d liu sp trong KM
   */
  savePromotions(){
    this.data = {
      dataBuy: [],
      dataGift: []
    } 
    // clear data sp mua  lu DB
    for (let i = 0; i < this.buy.length; i++) {
      let dt_Buy: any = {
        id_product: this.buy[i]._id,
        ratio: this.buy[i].ratio ? this.buy[i].ratio : 1,
        quantity: this.buy[i].quantity_discount,
        ptype: this.buy[i].type,
        // id_subproduct: this.buy[i].id_subproduct ? this.buy[i].id_subproduct : ''
      }
      if(this.buy[i].id_subproduct) {
        dt_Buy = {...dt_Buy, id_subproduct: this.buy[i].id_subproduct}
      }
      if(dt_Buy.ptype == 5) {
        let combos = this.buy[i].combos.map(item => {
          let combo ={ 
            id_product: item.id_product, 
            ptype: item.type,
            price: item.price,
            ratio: item.ratio ? item.ratio : 1,
            quantity: item.quantity,
          }
          if(item.id_subproduct) combo['id_subproduct'] = item.id_subproduct
          return  combo
        })
        dt_Buy = {...dt_Buy, combos: combos}
      }
      this.data.dataBuy.push(dt_Buy);
      
    }
    // clear data sp tng  lu DB
    for (let j = 0; j < this.gift.length; j++) {
      let dt_gift: any = {
        id_product: this.gift[j]._id,
        ratio: this.gift[j].ratio ? this.gift[j].ratio : 1,
        quantity: this.gift[j].quantity_discount,
        price: this.gift[j].price_gift,
        ptype: this.gift[j].type,
      }
      if(this.gift[j].id_subproduct) {
        dt_gift = {...dt_gift, id_subproduct: this.gift[j].id_subproduct}
      }
      if(dt_gift.ptype == 5) {
        let combos = this.gift[j].combos.map(item => {
          let combo ={ 
            id_product: item.id_product, 
            ptype: item.type,
            price: item.price,
            ratio: item.ratio ? item.ratio : 1,
            quantity: item.quantity,
          }
          if(item.id_subproduct) combo['id_subproduct'] = item.id_subproduct
          return  combo
        })
        dt_gift = {...dt_gift, combos: combos}
      }
      this.data.dataGift.push(dt_gift)
    }    
   this.updateProducts.emit(this.data)
    
  }

  /**
   * xa sp mua
   */
  public remove(item) {
    this.modal.confirm({
      nzTitle: `Xa <strong><i>${item.name}</i></strong> ?`,
      // nzContent: '<b>Xa sn</b>',
      nzOnOk: () =>  this.buy.splice(this.buy.indexOf(item), 1)
    });
  }

  /**
   * xa sp tng
   */
  removeGift(item) {
    this.modal.confirm({
      nzTitle: `Xa <strong><i>${item.name}</i></strong> ?`,
      // nzContent: '<b>Xa sn</b>',
      nzOnOk: () =>  {
        this.gift.splice(this.gift.indexOf(item), 1)
        this.gift_length.emit(this.gift.length)
      }
    });
  }

  // -------modal edit unit price-------
  temp: any;
  isVisibleEditUnitPrice: boolean = false;
  price: any = 0;

  /**
   * m modal nhp gi gim sp
   */
  openEditUnitPrices(detail) {
    this.temp = detail
    this.isVisibleEditUnitPrice = true;
    this.vhAlgorithm.waitingStack().then(() => {
      this.price = this.vhAlgorithm.vhnumeral('.unit_price');
    });
  }

  /**
   * x l sk nhn ok modal nhp gi gim sp
   */
  handleEditUnit() {
    if(this.price.getRawValue() && this.price.getRawValue() >= 0) {
      this.temp.price_gift = parseFloat(this.price.getRawValue())
    } else {
      this.temp.price_gift = 0
    }
    this.isVisibleEditUnitPrice = false;
  }
  /**
   * ng modal nhp gi gim sp v ko lu
   */
  handleCancelEditUnit() {
    this.isVisibleEditUnitPrice = false;
  }

  // -------modal edit unit quantity-------
  buyQuantity: any;
  isVisibleEditUnitQuantity: boolean = false;
  quantity_discount: any;

  /**
   * m modal chnh sa s lng sp trong km
   */
  openEditUnitQuantity(detail) {
    this.buyQuantity = detail;
    this.isVisibleEditUnitQuantity = true;
    this.vhAlgorithm.waitingStack().then(() => {
      this.quantity_discount = this.vhAlgorithm.vhnumeral('.unit_quantity');
    });
  }

  /**
   * x l sk nhn ok modal chnh sa s lng sp trong km
   */
  handleEditUnitQuantity() {
    if (this.quantity_discount.getRawValue() >= 0) {
      this.buyQuantity.quantity_discount = parseFloat(this.quantity_discount.getRawValue());
    } else if (this.quantity_discount.getRawValue() < 0)
      this.vhComponent.alertMessageDesktop(
        "error",
        this.lang.translate('Invalid data, please check again')
      );
    this.isVisibleEditUnitQuantity = false;
  }
  /**
   * ng modal chnh sa s lng sp trong km
   */
  handleCancelEditUnitQuantity() {
    this.isVisibleEditUnitQuantity = false;
  }

}
