import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { VhAlgorithm, VhQuerySales } from 'ionic-vhframeworks';
import { NzModalService } from 'ng-zorro-antd/modal';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { VhPromotionDetail, VhPromotions } from 'src/app/sales/interface/vh-promotions-money-rate';
import { LanguageService } from 'src/app/sales/services/language.service';

@Component({
  selector: 'app-discount-price-total-invoice',
  templateUrl: './discount-price-total-invoice.component.html',
  styleUrls: ['./discount-price-total-invoice.component.scss']
})
export class DiscountPriceTotalInvoiceComponent implements OnInit {

  @Output() updateProduct = new EventEmitter<any>();
  @Output() gift_length = new EventEmitter<number>();
  searchProductList: boolean = false;
  data = []
  constructor(
    private vhComponent: VhComponent,
    private lang: LanguageService,
    private modal: NzModalService,
    public vhQuerySales: VhQuerySales,
    public vhAlgorithm: VhAlgorithm,
  ) { }

  ngOnInit(): void {
  }
  
  /**
   * m drawer chn sp vo ctkm
   */
  openSearchProductList() {
    this.searchProductList = true;
  }

  // -------modal edit unit price-------
  tempDetail: any;
  isVisibleEditUnitPrice: boolean = false;
  new_price: any;

  /**
   * m modal nhp gi gim sp
   */
  openEditUnitPrice(detail) {
    this.tempDetail = detail;
    this.isVisibleEditUnitPrice = true;
      this.vhAlgorithm.waitingStack().then(() => {
        this.new_price = this.vhAlgorithm.vhnumeral('.unit_price');
      });
  }

  /**
   * x l sk nhn ok modal nhp gi gim sp
   */
  handleEditUnit() {
    if (this.new_price.getRawValue() >= 0) {
      this.tempDetail.price_gift = parseFloat(this.new_price.getRawValue());     
    } else if (this.new_price.getRawValue() < 0)
      this.vhComponent.showToast(
        2000,
        this.lang.translate('Gi mi phi ln hn 0. Vui lng nhp li !'),
        'alert-toast'
      );
    this.isVisibleEditUnitPrice = false;
  }
  /**
   * ng modal nhp gi gim sp v ko lu
   */
  handleCancelEditUnit() {
    this.isVisibleEditUnitPrice = false;
  }


  // -------modal edit unit discount-------
  temp: any;
  isVisibleEditDiscount: boolean = false;
  new_discount: any;

  /**
   * m modal chnh sa s lng p dng sp trong km
   */
  openEditDiscount(detail) {    
    this.temp = detail;
    this.isVisibleEditDiscount = true;
    this.vhAlgorithm.waitingStack().then(() => {
      this.new_discount = this.vhAlgorithm.vhnumeral('.unit_price_discount');
    });
  }

  /**
   * x l sk nhn ok modal chnh sa s lng p dng sp trong km
   */
  handleEditDiscount() {
    if (this.new_discount.getRawValue() >= 0) {
      this.temp.quantity_discount = parseFloat(this.new_discount.getRawValue());     
    } else if (this.new_discount.getRawValue() < 0)
      this.vhComponent.showToast(
        2000,
        this.lang.translate('S lng khng th nh hn 0. Vui lng nhp li !'),
        'alert-toast'
      );
    this.isVisibleEditDiscount = false;
  }
  /**
   * ng modal chnh sa s lng p dng sp trong km
   */
  handleCancelEditDiscount() {
    this.isVisibleEditDiscount = false;
  }
  
  /**
   * bt sk thay i unit ca sp
   */
  getPrice(item, e) {
    item.price = item.unit_array.find(it => it.unit == e).price;
    item.ratio = item.unit_array.find(it => it.unit == e).ratio
    item.unit = e
  }

  /**
   * bt sk nhn nt lu km , output v component cha d liu sp trong KM
   */
  savePromotions() {
    let products = [];
    for (let i = 0; i < this.data.length; i++) {
      let new_item: any = {
        id_product: this.data[i]._id,
        ratio: this.data[i].ratio ? this.data[i].ratio : 1,
        price: this.data[i].price_gift,
        quantity: this.data[i].quantity_discount,
        ptype: this.data[i].type,
      };
      if(this.data[i].id_subproduct) {
        new_item = {...new_item, id_subproduct: this.data[i].id_subproduct}
      }
      if(new_item.ptype == 5) {
        let combos = this.data[i].combos.map(item => {
          let combo ={ 
            id_product: item.id_product, 
            ptype: item.type,
            price: item.price,
            ratio: item.ratio ? item.ratio : 1,
            quantity: item.quantity,
          }
          if(item.id_subproduct) combo['id_subproduct'] = item.id_subproduct
          return  combo
        })
        new_item = {...new_item, combos: combos}
      }
      products.push(new_item);
    }
    this.updateProduct.emit(products)
  }

  /**
   * x l danh sch sp  chn khi ng drawer chn sp vo ctkm
   */
  closeSearchProductList(data){
    if (data) {
      for (let i = 0; i < data.length; i++) {
        let unit_array = [];
        data[i] = {...data[i],unit_array }
        // let unitMain = {
        //   unit: data[i].unit,
        //   ratio: 1,
        //   barcode: data[i].barcode,
        //   price: data[i].price,
        //   price2: data[i].price2,
        //   price_important: data[i].price_important,
        // };
        // data[i]['unit_array'].unshift(unitMain);
        if (data[i].units) {
          data[i]['unit_array'] = data[i]['unit_array'].concat(data[i].units)
        }
        let price_gift = 0;
        let quantity_discount = 1;
        data[i] = {...data[i], price_gift: price_gift, quantity_discount: quantity_discount}
        if((this.data.length + data.length) <= 100) {
          this.data.unshift(data[i]);
        } else {
          this.modal.error({
            nzTitle: 'S lng sn phm vt qu gii hn',
            nzContent: 'Vui lng to thm khuyn mi khc !'
          });
          break;
        }
      }
    }
    this.gift_length.emit(this.data.length)
    this.searchProductList = false;    
  }

  /**
   * xa sp KM
   */
  public remove(item) {
    this.modal.confirm({
      nzTitle: `Xa <strong><i>${item.name}</i></strong> ?`,
      // nzContent: '<b>Xa sn</b>',
      nzOnOk: () =>  {
        this.data.splice(this.data.indexOf(item), 1)
        this.gift_length.emit(this.data.length)
      }
    });
  }

}
