import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { Router } from '@angular/router';
import { VhAlgorithm, VhQuerySales } from 'ionic-vhframeworks';
import { NzModalService } from 'ng-zorro-antd/modal';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import {
  VhPromotionDetail,
  VhPromotions,
} from 'src/app/sales/interface/vh-promotions-money-rate';
import { LanguageService } from 'src/app/sales/services/language.service';

@Component({
  selector: 'app-discount-percent-product',
  templateUrl: './discount-percent-product.component.html',
  styleUrls: ['./discount-percent-product.component.scss'],
})
export class DiscountPercentProductComponent implements OnInit {
  @Output() changeVl = new EventEmitter<any>();
  @Input() promotion_all_products
  data: any = [];
  searchProductList: boolean = false;
  constructor(
    private router: Router,
    public vhAlgorithm: VhAlgorithm,
    private vhComponent: VhComponent,
    private lang: LanguageService,
    private modal: NzModalService,
    public vhQuerySales: VhQuerySales,
  ) {}

  ngOnInit(): void {
  }

  /**
   * bt sk thay i unit ca sp
   */
  getPrice(item, e) {
    item.price = item.unit_array.find(it => it.unit == e).price;
    item.ratio = item.unit_array.find(it => it.unit == e).ratio
    item.unit = e
  }

  /**
   * bt sk nhn nt lu km , output v component cha d liu sp trong KM
   */
  savePromotions() {
    let products = [];
    for(let i = 0; i < this.data.length; i++) {
      let new_item: any = {
        id_product: this.data[i]._id,
        ratio: this.data[i].ratio ? this.data[i].ratio : 1,
        price_origin: this.data[i].price,
        percent_reduce: this.data[i].discount ? this.data[i].discount : 0,
        ptype: this.data[i].type,
        quantity: this.data[i].quantity_discount
      };
      if(this.data[i].id_subproduct) {
        new_item = {...new_item, id_subproduct: this.data[i].id_subproduct}
      }
      if(new_item.ptype == 5) {
        let combos = this.data[i].combos.map(item => {
          return { 
            id_product: item.id_product, 
            ptype: item.type,
            price: item.price,
            ratio: item.ratio ? item.ratio : 1,
            quantity: item.quantity,
            id_category: this.data[i].id_category
          }
        })
        new_item = {...new_item, combos: combos}
      }
      products.push(new_item);
    }
    this.changeVl.emit(products);
  }

  /** Modal edit all price */
  new_discount: any = 0;
  isVisibleEditUnitPriceAll: boolean = false;
  percent_all: number = 0;
  disabled_all: boolean = true;
  /** M modal */
  openEditUnitPriceAll() {
    this.isVisibleEditUnitPriceAll = true;
  }

  /** sa phn model % */
  handleEditUnitAll() {
    if (
      parseFloat(document.getElementById('unit_price_all')['value']) >= 0 &&
      parseFloat(document.getElementById('unit_price_all')['value']) <= 100
    ) {
      for (let i = 0; i < this.data.length; i++) {
        this.data[i].discount = parseFloat(document.getElementById('unit_price_all')['value']);
        this.disabled_all = true;
        this.isVisibleEditUnitPriceAll = false;
      }
    } else {
      this.disabled_all = false;
      
    }
  }

  /** Tt modal */
  handleCancelEditUnitAll() {
    this.isVisibleEditUnitPriceAll = false;
  }

  // -------modal edit unit price-------
  tempDetail: any;
  isVisibleEditUnitPrice: boolean = false;
  price: any = 0;
  disabled: boolean = true;

  /**
   * m modal nhp gi gim sp
   */
  openEditUnitPrice(detail: VhPromotionDetail) {
    this.tempDetail = detail;
    this.isVisibleEditUnitPrice = true;
  }

  /**
   * x l sk nhn ok modal nhp gi gim sp
   */
  handleEditUnit() {
    if (
      parseFloat(document.getElementById('unit_price')['value']) >= 0 &&
      parseFloat(document.getElementById('unit_price')['value']) <= 100
    ) {
      this.tempDetail.discount = parseFloat(document.getElementById('unit_price')['value']);
      // this.tempDetail.new_price = this.tempDetail.price *
      //   (1 - parseFloat(document.getElementById('unit_price')['value']) / 100);
      this.disabled = true;
      this.isVisibleEditUnitPrice = false;
    } else {
      this.disabled = false;
    }
  }
  /**
   * ng modal nhp gi gim sp v ko lu
   */
  handleCancelEditUnit() {
    this.isVisibleEditUnitPrice = false;
  }

  /**
   * m drawer chn sp vo ctkm
   */
  openSearchProductList() {
    this.searchProductList = true;
  }

  /**
   * x l danh sch sp  chn khi ng drawer chn sp vo ctkm
   */
  closeSearchProductList(data) {
    if (data) {
      for (let i = 0; i < data.length; i++) {
        let unit_array = [];
        data[i] = {...data[i],unit_array }
        // let unitMain = {
        //   unit: data[i].unit,
        //   ratio: 1,
        //   barcode: data[i].barcode,
        //   price: data[i].price,
        //   price2: data[i].price2,
        //   price_important: data[i].price_important,
        // };
        // data[i]['unit_array'].unshift(unitMain);
        if (data[i].units) {
          data[i]['unit_array'] = data[i]['unit_array'].concat(data[i].units)
        }
        let discount = 0;
        let quantity_discount = 1
        data[i] = {...data[i], discount: discount, quantity_discount: quantity_discount}
        if((this.data.length + data.length) <= 100) {
          this.data.unshift(data[i]);
        } else {
          this.modal.error({
            nzTitle: 'S lng sn phm vt qu gii hn',
            nzContent: 'Vui lng to thm khuyn mi khc !'
          });
          break;
        }
        
      }
      
    }
    this.searchProductList = false;
  }

  // -------modal edit unit quantity-------
  temp: any;
  isVisibleEditUnitQuantity: boolean = false;
  quantity: any = 0;

  /**
   * m modal chnh sa s lng sp trong km
   */
  openEditUnitQuantity(detail) {
    this.temp = detail;
    this.isVisibleEditUnitQuantity = true;
    this.vhAlgorithm.waitingStack().then(() => {
      this.quantity = this.vhAlgorithm.vhnumeral('.unit_quantity');
    });
  }

  /**
   * x l sk nhn ok modal chnh sa s lng sp trong km
   */
  handleEditUnitQuantity() {
    if (this.quantity.getRawValue() >= 0) {
      this.temp.quantity_discount = parseFloat(this.quantity.getRawValue());
    } else if (this.quantity.getRawValue() < 0)
      this.vhComponent.alertMessageDesktop(
        'error',
        this.lang.translate('Invalid data, please check again')
      );
    this.isVisibleEditUnitQuantity = false;
  }
  /**
   * ng modal chnh sa s lng sp trong km
   */
  handleCancelEditUnitQuantity() {
    this.isVisibleEditUnitQuantity = false;
  }

  /**
   * xa sp KM
   */
  public remove(item) {
    this.modal.confirm({
      nzTitle: `Xa <strong><i>${item.name}</i></strong> ?`,
      // nzContent: '<b>Xa sn</b>',
      nzOnOk: () =>  this.data.splice(this.data.indexOf(item), 1)
    });
  }
}
