import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { VhAlgorithm, VhQuerySales } from 'ionic-vhframeworks';
import { NzModalService } from 'ng-zorro-antd/modal';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { LanguageService } from 'src/app/sales/services/language.service';

@Component({
  selector: 'app-detail-discount-take-gift',
  templateUrl: './detail-discount-take-gift.component.html',
  styleUrls: ['./detail-discount-take-gift.component.scss']
})
export class DetailDiscountTakeGiftComponent implements OnInit {

  @Output() updateProducts = new EventEmitter<any>();
  @Output() gift_length = new EventEmitter<any>();
  @Input() data_buy: any = [];
  @Input() data_gift: any = [];

  searchProductList: boolean = false;
  searchProductGift: boolean = false;
  quantity: number = 1;
  data = {
    dataBuy: [],
    dataGift: []
  }
  constructor(
    public vhQuerySales: VhQuerySales,
    public vhAlgorithm: VhAlgorithm,
    private lang: LanguageService,
    private vhComponent: VhComponent,
    private modal: NzModalService


  ) { }
  
  
  ngOnInit(): void {
    this.getDataBuyById();
    this.getDataGiftById();
  }

   /** Ly d liu sp mua local */
   getDataBuyById() {
    for (let i = 0; i < this.data_buy.length; i++) {
      let product = this.vhQuerySales.getlocalDetailProduct(this.data_buy[i].id_subproduct ? this.data_buy[i].id_subproduct :this.data_buy[i].id_product);  
      this.data_buy[i].name = product.name
      // this.data_buy[i].price_unit = product.price
      product['unit_array'] = [];
      if (product.units) {
        this.data_buy[i]['unit_array'] = product['unit_array'].concat(product.units); //thm mng unit  chn unit, chn gi
        this.data_buy[i]['price_unit'] = this.data_buy[i]['unit_array'].find(item => item.ratio == this.data_buy[i].ratio).price
      } else {
        this.data_buy[i] = {...this.data_buy[i], unit_array: product['unit_array']};
      }
    }
  }

  mathRound(value) {
    return Math.round(value)
  }

  /** Ly d liu sp tng local */
  getDataGiftById() {
    for (let i = 0; i < this.data_gift.length; i++) {
      let product = this.vhQuerySales.getlocalDetailProduct(this.data_gift[i].id_subproduct ? this.data_gift[i].id_subproduct :this.data_gift[i].id_product);  
      this.data_gift[i].name = product.name
      // this.data_gift[i].price_unit = product.price
      product['unit_array'] = [];
      if (product.units) {
        this.data_gift[i]['unit_array'] = product['unit_array'].concat(product.units); //thm mng unit  chn unit, chn gi
        // this.data_gift[i]['price_unit'] = this.data_gift[i]['unit_array'].find(item => item.unit == this.data_gift[i].unit).price
        this.data_gift[i]['price_unit'] = this.data_gift[i].price

      } else {
        let price_unit = this.data_gift[i].price
        this.data_gift[i] = {...this.data_gift[i], unit_array: product['unit_array'], price_unit: price_unit};
      }
    }
  }

  /**
   * m drawer chn sp mua
   */
  openSearchProductList() {
    this.searchProductList = true;
  }

  /**
   * m drawer chn sp tng
   */
  openSearchProductGift() {
    this.searchProductGift = true
  }

  
  /**
   * bt sk thay i unit ca sp
   */
  getPrice(item, e) {
    // item.price_unit = item.unit_array.find(it => it.unit == e).price;
    item.ratio = e
    item.unit = item.unit_array.find(it => it.ratio == e).unit
  }

  /**
   * bt sk ng drawer chn sp tng
   * @param data 
   */
  closeSearchProductGift(data) {
    if (data) {
      for(let i = 0; i < data.length; i++) {
        let new_item: any = {
          id_product: data[i]._id,
          price_unit: 0,
          ratio: data[i].ratio ? data[i].ratio : 1,
          unit: data[i].unit,
          quantity: 1,
          ptype: data[i].type,
          // id_subproduct: data[i].id_subproduct,
          name: data[i].name,
          unit_array: data[i].units  
        }
        if(data[i].id_subproduct) {
          new_item = {...new_item, id_subproduct: data[i].id_subproduct}
        }
        if(new_item.ptype == 5) {          
          let combos = data[i].combos.map(item => {
            return {
              id_product: item.id_product,
              id_subproduct: item.id_subproduct,
              ptype: item.type,
              price: item.price,
              ratio: item.ratio ? item.ratio : 1,
              unit: item.unit ? item.unit : '',
              quantity: item.quantity,
              id_category: data[i].id_category
            }
          })
          new_item = {...new_item, combos: combos}
        }
        if((this.data_gift.length + data.length) <= 100) {
          this.data_gift.unshift(new_item)
        } else {
          this.modal.error({
            nzTitle: 'S lng sn phm vt qu gii hn',
            nzContent: 'Vui lng to thm khuyn mi khc !'
          });
          break;
        }
      }
    }
    this.gift_length.emit(this.data_gift.length)
    this.searchProductGift = false;

  }

  /**
   * bt sk ng drawer chn sp mua
   * @param data 
   */
  closeSearchProductList(data) {     
    if (data) {
      for(let i = 0; i < data.length; i++) {
        let new_item: any = {
          id_product: data[i]._id,
          price_unit: data[i].price,
          ratio: data[i].ratio ? data[i].ratio : 1,
          unit: data[i].unit,
          quantity: 1,
          ptype: data[i].type,
          // id_subproduct: data[i].id_subproduct,
          name: data[i].name,
          unit_array: data[i].units 
        }
        if(data[i].id_subproduct) {
          new_item = {...new_item, id_subproduct: data[i].id_subproduct}
        }
        if(new_item.ptype == 5) {          
          let combos = data[i].combos.map(item => {
            return {
              id_product: item.id_product, 
              id_subproduct: item.id_subproduct,
              ptype: item.type,
              price: item.price,
              ratio: item.ratio ? item.ratio : 1,
              unit: item.unit ? item.unit : '',
              quantity: item.quantity,
              id_category: data[i].id_category
            }
          })
          new_item = {...new_item, combos: combos}
        }
        if((this.data_buy.length + data.length) <= 100) {
          this.data_buy.unshift(new_item)
        } else {
          this.modal.error({
            nzTitle: 'S lng sn phm vt qu gii hn',
            nzContent: 'Vui lng to thm khuyn mi khc !'
          });
          break;
        }
       
      }
    }

    this.searchProductList = false;
  }
  /**
   * bt sk nhn nt lu km , output v component cha d liu sp trong KM
   */
  savePromotions(){  
    this.data = {
      dataBuy: [],
      dataGift: []
    }  
    for (let i = 0; i < this.data_buy.length; i++) {
      let dt_Buy: any = {
        id_product: this.data_buy[i].id_product,
        ratio: this.data_buy[i].ratio ? this.data_buy[i].ratio : 1,
        quantity: this.data_buy[i].quantity,
        ptype: this.data_buy[i].ptype,
        // id_subproduct: this.data_buy[i].id_subproduct
      }
      if(this.data_buy[i].id_subproduct) {
        dt_Buy = {...dt_Buy, id_subproduct: this.data_buy[i].id_subproduct}
      }
      if(dt_Buy.ptype == 5) {
        let combos = this.data_buy[i].combos.map(item => {
          let combo ={ 
            id_product: item.id_product, 
            ptype: item.type,
            price: item.price,
            ratio: item.ratio ? item.ratio : 1,
            quantity: item.quantity,
          }
          if(item.id_subproduct) combo['id_subproduct'] = item.id_subproduct
          return  combo
        })
        dt_Buy = {...dt_Buy, combos: combos}
      }

      this.data.dataBuy.push(dt_Buy);
    }
    for (let j = 0; j < this.data_gift.length; j++) {
      let dt_gift: any = {
        id_product: this.data_gift[j].id_product,
        ratio: this.data_gift[j].ratio ? this.data_gift[j].ratio : 1,
        quantity: this.data_gift[j].quantity,
        price: this.data_gift[j].price_unit,
        ptype: this.data_gift[j].ptype,
        // id_subproduct: this.data_gift[j].id_subproduct
      }
      if(this.data_gift[j].id_subproduct) {
        dt_gift = {...dt_gift, id_subproduct: this.data_gift[j].id_subproduct}
      }
      if(dt_gift.ptype == 5) {
        let combos = this.data_gift[j].combos.map(item => {
          let combo ={ 
            id_product: item.id_product, 
            ptype: item.type,
            price: item.price,
            ratio: item.ratio ? item.ratio : 1,
            quantity: item.quantity,
          }
          if(item.id_subproduct) combo['id_subproduct'] = item.id_subproduct
          return  combo
        })
        dt_gift = {...dt_gift, combos: combos}
      }
      this.data.dataGift.push(dt_gift)
    }
    this.updateProducts.emit(this.data)
    }

  /**
   * xa sp mua
   */
  public remove(item) {
    this.modal.confirm({
      nzTitle: `Xa <strong><i>${item.name}</i></strong> ?`,
      // nzContent: '<b>Xa sn</b>',
      nzOnOk: () =>  this.data_buy.splice(this.data_buy.indexOf(item), 1)
    });
  }

  /**
   * xa sp tng
   */
  removeGift(item) {
    this.modal.confirm({
      nzTitle: `Xa <strong><i>${item.name}</i></strong> ?`,
      // nzContent: '<b>Xa sn</b>',
      nzOnOk: () =>  {
        this.data_gift.splice(this.data_gift.indexOf(item), 1)
        this.gift_length.emit(this.data_gift.length)
      }
    });

  }

  // -------modal edit unit price-------
  temp: any;
  isVisibleEditUnitPrice: boolean = false;
  price: any = 0;

  /**
   * m modal nhp gi gim sp
   */
  openEditUnitPrices(detail) {
    this.temp = detail
    this.isVisibleEditUnitPrice = true;
    this.vhAlgorithm.waitingStack().then(() => {
      this.price = this.vhAlgorithm.vhnumeral('.unit_price');
    });
  }

  /**
   * x l sk nhn ok modal nhp gi gim sp
   */
  handleEditUnit() {
    if(this.price.getRawValue() && this.price.getRawValue() >= 0) {
      this.temp.price_unit = parseFloat(this.price.getRawValue())
    } else {
      this.temp.price_unit = 0
    }
    this.isVisibleEditUnitPrice = false;
  }
  /**
   * ng modal nhp gi gim sp v ko lu
   */
  handleCancelEditUnit() {
    this.isVisibleEditUnitPrice = false;
  }

   // -------modal edit unit quantity-------
   buyQuantity: any;
   isVisibleEditUnitQuantity: boolean = false;
   quantity_discount: any;
 
  /**
   * m modal chnh sa s lng sp trong km
   */
   openEditUnitQuantity(detail) {
     this.buyQuantity = detail;
     this.isVisibleEditUnitQuantity = true;
     this.vhAlgorithm.waitingStack().then(() => {
       this.quantity_discount = this.vhAlgorithm.vhnumeral('.unit_quantity');
     });
   }
 
  /**
   * x l sk nhn ok modal chnh sa s lng sp trong km
   */
   handleEditUnitQuantity() {
     if (this.quantity_discount.getRawValue() >= 0) {
       this.buyQuantity.quantity = parseFloat(this.quantity_discount.getRawValue());
     } else if (this.quantity_discount.getRawValue() < 0)
       this.vhComponent.alertMessageDesktop(
         "error",
         this.lang.translate('Invalid data, please check again')
       );
     this.isVisibleEditUnitQuantity = false;
   }
   /**
    * ng modal chnh sa s lng sp trong km
    */
   handleCancelEditUnitQuantity() {
     this.isVisibleEditUnitQuantity = false;
   }
}
