import {Component } from '@angular/core';
import { ActivatedRouteSnapshot, Router, RouterStateSnapshot, UrlTree } from '@angular/router';
import { VhAlgorithm, VhQuerySales } from 'ionic-vhframeworks';
import { NzModalService } from 'ng-zorro-antd/modal';
import { Observable } from 'rxjs';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { LanguageService } from 'src/app/sales/services/language.service';

@Component({
  selector: 'app-add',
  templateUrl: './add.component.html',
  styleUrls: ['./add.component.scss']
})
export class AddComponent {
  public startTimeBackup: string = (new Date(new Date().setHours(0, 0, 0, 0))).toISOString();
  public endTimeBackup: string = (new Date(new Date().setHours(23, 59, 59, 59))).toISOString();
  tableHeight: any;
  checked_customer: false;
  checked_member: false;
  visible_add: boolean = false;
  promotion_all_products: boolean = false;
  price_reduce:any;
  percent_reduce:any;
  billTotal: any;
  promotions_maximum: number = 0;
  gift_length: number = 0;
  list: Array<any> = [
    { value: 1, name: "Discount in amount" },
    { value: 2, name: "Discount in percent" }, 
    { value: 3, name: "Sn phm ng gi" }, 
    { value: 4, name: "Gim gi tin khi mua  SL sn phm"},
    { value: 5, name: "Gim phn trm khi mua  SL sn phm"},
    { value: 6, name: "Tng qu khi mua nhiu sn phm"},
    { value: 7, name: "Tng qu theo tng gi tr n hng"},
  ]
  percent: number = 0;
  promotionType: 1 | 2 | 3 | 4 | 5 | 6 | 7 = 1;
  promotion_name: string = '';
  discount_price: any;
  dataStore:any;
  quantity_promotions_all = 1;
  variable_enable = false;
  constructor(
    private router: Router,
    private vhComponent: VhComponent,
    private lang: LanguageService,
    private vhQuerySales: VhQuerySales,
    public vhAlgorithm: VhAlgorithm,
    private modal: NzModalService

  ) {
      this.dataStore = this.router.getCurrentNavigation().extras.state
      this.eventKeypress()
  }
  /**
   * bt sk thay i ngy bt u KM
   */
  setDateStart(value) {
    if (Date.parse(value) - Date.parse(this.endTimeBackup) > 0)
      this.vhComponent.alertMessage("", "", this.lang.translate("Staring date must be less than or the same as ending date"), "OK")
        .then(() => {
          return this.startTimeBackup
        })
    else this.startTimeBackup = value
  }
  /**
   * bt sk thay i ngy kt thc KM
   */
  setDateEnd(value) {
    if (Date.parse(value) - Date.parse(this.startTimeBackup) < 0)
      this.vhComponent.alertMessage("", "", this.lang.translate("Staring date must be less than or the same as ending date"), "OK")
        .then(() => {
          return this.endTimeBackup
        })
    else this.endTimeBackup = value
  }
  /**
   * bt sk thay i loi KM
   */
  changePromotionType(value) { 
    this.promotion_all_products = false
    this.promotionType = value;
    if(value == 7) {
      this.vhAlgorithm.waitingStack().then(() => {
        this.billTotal = this.vhAlgorithm.vhnumeral(".price_discout");
      });
    }
  }

  /**
   * bt sk thit lp p dng cho tt c sp
   */
  setAllProduct(event){
    if(event) this.initNumberral()
  }
  /**
   * to cleavejs cho gi gim  x l
   */
  initNumberral(){
    this.vhAlgorithm.waitingStack().then(() => {
      if(this.promotionType == 1 || this.promotionType == 3) this.price_reduce = this.vhAlgorithm.vhnumeral(".price_discout");
    });
  }

  goBack() {
    this.isExit = true
    this.vhComponent.alertConfirm(this.lang.translate("Confirm"), "", this.lang.translate("Are you sure to cancel the promotions"), "OK", (this.lang.translate("Cancel"))).then(() => {
       this.router.navigate(["/sales/dashboard/manage/sales-program/promotions"],{state:this.dataStore}) 
    }).catch(() => { });
  }
  eventKeypress() {
    // ----lng nghe s kin f4  show nz-drawer search product----
    window.addEventListener('keydown', (event: KeyboardEvent) => {
      const keycode = event.keyCode;
      if (keycode === 114) {
        event.preventDefault();
      }
      if (keycode === 115) {
       
      }
    });
  }

  /**
   * lu khuyn mi
   */
  SavePromt(ev) {   
    // x l d liu  lu vo DB  cho tng loi km
    let promotion: any = {};
    if(this.promotionType == 6) {
      promotion = {
        name: this.promotion_name,
        date_start: this.startTimeBackup,
        date_end: this.endTimeBackup,
        status: true,
        type: this.promotionType,
        promotions_maximum: this.promotions_maximum,
        products: ev.dataBuy,
        products_gift: ev.dataGift
      }
    } else if (this.promotionType == 7) {
      promotion = {
        name: this.promotion_name,
        date_start: this.startTimeBackup,
        date_end: this.endTimeBackup,
        status: true,
        type: this.promotionType,
        products_gift: ev,
        promotions_maximum: this.promotions_maximum,
        total_bill_value: parseFloat(this.billTotal.getRawValue()),
        variable_enable: this.variable_enable
      }
    } else if (this.promotionType == 4){
      promotion= {
        name: this.promotion_name,
        date_start: this.startTimeBackup,
        date_end: this.endTimeBackup,
        status: true,
        type: this.promotionType,
        products: ev
      }
    } else if (this.promotionType == 5){
      promotion= {
        name: this.promotion_name,
        date_start: this.startTimeBackup,
        date_end: this.endTimeBackup,
        status: true,
        type: this.promotionType,
        products: ev
      }
    } else if (this.promotionType == 3) {
      promotion= {
        name: this.promotion_name,
        date_start: this.startTimeBackup,
        date_end: this.endTimeBackup,
        status: true,
        type: this.promotionType,
        promotion_all_products: this.promotion_all_products,
        products: ev
      }
      if(this.promotion_all_products) promotion['quantity_promotions_all']  = this.quantity_promotions_all
    } else {
      promotion= {
        name: this.promotion_name,
        date_start: this.startTimeBackup,
        date_end: this.endTimeBackup,
        status: true,
        promotion_all_products: this.promotion_all_products,
        type: this.promotionType,
        products: ev
      }
      if(this.promotion_all_products) promotion['quantity_promotions_all']  = this.quantity_promotions_all
    }
    

    this.vhComponent.alertConfirm(this.lang.translate("Confirm"), "", this.lang.translate("Save") + "?", 'OK', this.lang.translate("Cancel"))
    .then(() => {
      this.vhComponent.showLoading("", "transparent-loading").then(() => {
      if (promotion.promotion_all_products) {
          if (this.promotion_name == '') {
              this.vhComponent.hideLoading(0).then(() => this.vhComponent.alertMessageDesktop("error", `${this.lang.translate("Vui lng khng  trng tn khuyn mi !")}`))
            }  
            else {
              switch (promotion.type) {
                case 1: {
                  if(!this.price_reduce.getRawValue()) {
                      this.vhComponent.hideLoading(0).then(() => this.vhComponent.alertMessageDesktop("error", `${this.lang.translate("Vui lng nhp gi tr")}`))
                  } else {
                    promotion = { ...promotion, price_reduce: parseFloat(this.price_reduce.getRawValue())}
                    delete promotion.products
                    this.addPromotion(promotion)
                    break
                  }
                  break;
                };
                case 3: {
                  if(!this.price_reduce.getRawValue()) {
                      this.vhComponent.hideLoading(0).then(() => this.vhComponent.alertMessageDesktop("error", `${this.lang.translate("Vui lng nhp gi tr")}`))
                  } else {
                    promotion = { ...promotion, price_same: parseFloat(this.price_reduce.getRawValue())}
                    delete promotion.products
                    this.addPromotion(promotion)
                    break
                  }
                  break;
                }
                case 2: {
                    let percent =  parseFloat(this.percent_reduce)
                    if(percent> 100 || percent < 0) {
                      this.vhComponent.hideLoading(0).then(() => this.vhComponent.alertMessageDesktop("error", `${this.lang.translate("Gi tr % <= 100 hoc >= 0")}`))
                    }
                    else if(this.percent_reduce === undefined) {
                      this.vhComponent.hideLoading(0).then(() => this.vhComponent.alertMessageDesktop("error", `${this.lang.translate("Vui lng nhp gi tr %")}`))
                    }
                    else {
                      promotion = { ...promotion, percent_reduce: percent}
                      delete promotion.products
                      this.addPromotion(promotion)
                    }
                  
                    break;
                }               
              }
            }
          }
      else {
          if (this.promotion_name == '') {
            this.vhComponent.hideLoading(0).then(() => this.vhComponent.alertMessageDesktop("error", `${this.lang.translate("Vui lng khng  trng tn khuyn mi !")}`))
          } else if (this.promotionType !== 7 && promotion.products.length == 0)
            {
            this.vhComponent.hideLoading(0).then(() => this.vhComponent.alertMessageDesktop("error", `${this.lang.translate("Vui lng thm sn phm khuyn mi !")}`))
            }
          else if (
            this.promotionType == 7 &&
            promotion.products_gift.length == 0
          ) {
            this.vhComponent.hideLoading(0).then(() => this.vhComponent.alertMessageDesktop("error", `${this.lang.translate("Vui lng thm sn phm khuyn mi !")}`))
          } else if (this.promotionType == 7 && !promotion.total_bill_value)
            this.vhComponent.hideLoading(0).then(() => this.vhComponent.alertMessageDesktop("error", `${this.lang.translate("Vui lng nhp gi tr ha n !")}`))

          else {
            this.addPromotion(promotion);
          }
        }
      })
    }, () => {})

  }

  /**
   * thm km vo DB
   */
  addPromotion(promotion){
    this.vhQuerySales.addPromotion(promotion)
    .then((promt)=>{
       if (promt) {    
        this.vhComponent.showToast(1500, `${this.lang.translate("Promotions")} ${this.promotion_name} ${this.lang.translate("has been created successfully")}`, "success-toast");
           this.isExit = true
           let data = {
            check:true, // set true  khi quay v get li danh sch km mi nht
            type : this.dataStore.type,
            date : this.dataStore.date
           }
           this.router.navigate(['/sales/dashboard/manage/sales-program/promotions'],{state:data})
           this.vhComponent.hideLoading(0)
          }
          },error=>{
          this.vhComponent.hideLoading(0)
          
      });
  }
  
  /** ===============Thot trang==============*/
  isExit: boolean = false;
  checkDeactivate(currentRoute: ActivatedRouteSnapshot, currentState: RouterStateSnapshot, nextState?: RouterStateSnapshot): Observable<boolean | UrlTree> | Promise<boolean | UrlTree> | boolean | UrlTree {
    return nextState.url == '/login' ? true : (this.isExit ? this.isExit : this.vhComponent.alertConfirm(this.lang.translate("Confirm"), "", this.lang.translate("The changes will not be saved"), "OK", (this.lang.translate("Cancel"))).then(() => { return true }).catch(() => { return false }))
  }

  giftLength(e?) {
    this.promotions_maximum = e
    this.gift_length = e;
  }
  checkMaximum(){
    if(this.promotions_maximum == null || this.promotions_maximum > this.gift_length) this.promotions_maximum = this.gift_length
  }

}
