import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { Router } from '@angular/router';
import { VhAlgorithm, VhQuerySales } from 'ionic-vhframeworks';
import { NzModalService } from 'ng-zorro-antd/modal';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { LanguageService } from 'src/app/sales/services/language.service';

@Component({
  selector: 'app-detail-discount-many-product',
  templateUrl: './detail-discount-many-product.component.html',
  styleUrls: ['./detail-discount-many-product.component.scss']
})
export class DetailDiscountManyProductComponent implements OnInit {

  @Output() updateProduct = new EventEmitter<any>();
  @Input() data: any = []
  searchProductList: boolean = false;
  type: number = 4;
  discount_price_all: number = 0;
  constructor(
    private router: Router,
    public vhAlgorithm: VhAlgorithm,
    private vhComponent: VhComponent,
    private lang: LanguageService,
    public vhQuerySales: VhQuerySales,
    private modal: NzModalService

  ) { 
  }

  ngOnInit(): void {  
    this.getDataById()
  }

   /** Ly d liu local */
   getDataById() {
    for (let i = 0; i < this.data.length; i++) {
      let product = this.vhQuerySales.getlocalDetailProduct( this.data[i].id_subproduct ? this.data[i].id_subproduct : this.data[i].id_product);
      
      
      this.data[i].name = product.name
      // this.data[i].price_unit = product.price
      product['unit_array'] = [];
      if (product.units) {
        this.data[i]['unit_array'] = product['unit_array'].concat(product.units); //thm mng unit  chn unit, chn gi
        this.data[i]['price_unit'] = this.data[i]['unit_array'].find(item => item.ratio == this.data[i].ratio).price
        this.data[i]['discount'] = this.data[i].price_reduce
      } else {
        let discount = this.data[i].price_reduce
        this.data[i] = {...this.data[i], unit_array: product['unit_array'], discount: discount};
      }
    }
    
  }

  /**
   * bt sk thay i unit ca sp
   */
  getPrice(item, ratio) {
    item.price_unit = item.unit_array.find(it => it.ratio == ratio).price;
    item.price = item.price_unit > item.discount ? item.price_unit - item.discount : 0
    item.ratio =ratio
    item.unit =  item.unit_array.find(it => it.ratio == ratio).unit 
  }

  /**
   * bt sk nhn nt lu km , output v component cha d liu sp trong KM
   */
  savePromotions() {
    let products = [];
    for (let i = 0; i < this.data.length; i++) {
      let new_item: any = {
        id_product: this.data[i].id_product,
        quantity: this.data[i].quantity,
        ratio: this.data[i].ratio ? this.data[i].ratio : 1,
        price_reduce: this.data[i].discount ? this.data[i].discount : 0,
        ptype: this.data[i].ptype
      };
      if(this.data[i].id_subproduct) {
        new_item = {...new_item, id_subproduct: this.data[i].id_subproduct}
      }
      if(new_item.ptype == 5) {
        let combos = this.data[i].combos
        new_item = {...new_item, combos: combos}
      }
      products.push(new_item);
    }
    // 
    
    this.updateProduct.emit(products);
  }

  /**
   * m drawer chn sp vo ctkm
   */
  openSearchProductList() {
    this.searchProductList = true;
  }

  

  /**
   * x l danh sch sp  chn khi ng drawer chn sp vo ctkm
   */
  closeSearchProductList(data){    
    if (data) {
      for(let i = 0; i < data.length; i++) {
        let new_item: any = {
          id_product: data[i]._id,
          price_unit: data[i].price,
          ratio: data[i].ratio ? data[i].ratio : 1,
          unit: data[i].unit,
          discount: 0,
          name: data[i].name,
          quantity: 1,
          ptype: data[i].type,
          unit_array: data[i].units  
        }
        if(data[i].id_subproduct) {
          new_item = {...new_item, id_subproduct: data[i].id_subproduct}
        }
        if(new_item.ptype == 5) {          
          let combos = data[i].combos.map(item => {
            return {
              id_product: item.id_product, 
              ptype: item.type,
              price: item.price,
              ratio: item.ratio ? item.ratio : 1,
              unit: item.unit ? item.unit : '',
              quantity: item.quantity,
              id_category: data[i].id_category
            }
          })
          new_item = {...new_item, combos: combos}
        }
        if((this.data.length + data.length) <= 100) {
          this.data.unshift(new_item);
        } else {
          this.modal.error({
            nzTitle: 'S lng sn phm vt qu gii hn',
            nzContent: 'Vui lng to thm khuyn mi khc !'
          });
          break;
        }
      }
    }
    this.searchProductList = false;    
  }

  // -------modal edit unit price-------
  tempDetail: any;
  isVisibleEditUnitPrice: boolean = false;
  price: any = 0;

  /**
   * m modal nhp gi gim sp
   */
  openEditUnitPrice(detail) {
    this.tempDetail = detail;
    this.isVisibleEditUnitPrice = true;
    this.vhAlgorithm.waitingStack().then(() => {
      this.price = this.vhAlgorithm.vhnumeral('.unit_price');
    });
  }

  /**
   * x l sk nhn ok modal nhp gi gim sp
   */
  handleEditUnit() {
    if ( this.price.getRawValue() >= 0) {
      this.tempDetail.discount = parseFloat(this.price.getRawValue());
    } else if (this.price.getRawValue() < 0)
      this.vhComponent.showToast(
        2000,
        this.lang.translate('Invalid data, please check again'),
        'alert-toast'
      );
    this.isVisibleEditUnitPrice = false;
  }
  /**
   * ng modal nhp gi gim sp v ko lu
   */
  handleCancelEditUnit() {
    this.isVisibleEditUnitPrice = false;
  }


   // -------modal edit unit quantity-------
   temp: any;
    isVisibleEditUnitQuantity: boolean = false;
   quantity: any = 0;
 
  /**
   * m modal chnh sa s lng sp trong km
   */
   openEditUnitQuantity(detail) {
     this.temp = detail;
     this.isVisibleEditUnitQuantity = true;
     this.vhAlgorithm.waitingStack().then(() => {
       this.quantity = this.vhAlgorithm.vhnumeral('.unit_quantity');
     });
   }
 
  /**
   * x l sk nhn ok modal chnh sa s lng sp trong km
   */
   handleEditUnitQuantity() {
     if ( this.quantity.getRawValue() >= 0) {
       this.temp.quantity = parseFloat(this.quantity.getRawValue());
     } else if (this.quantity.getRawValue() < 0)
       this.vhComponent.alertMessageDesktop(
         'error',
         this.lang.translate('Invalid data, please check again')
         
       );
     this.isVisibleEditUnitQuantity = false;
   }
   /**
    * ng modal chnh sa s lng sp trong km
    */
   handleCancelEditUnitQuantity() {
     this.isVisibleEditUnitQuantity = false;
   }

  /**
   * xa sp KM
   */
  public remove(item) {
    this.modal.confirm({
      nzTitle: `Xa <strong><i>${item.name}</i></strong> ?`,
      // nzContent: '<b>Xa sn</b>',
      nzOnOk: () =>  this.data.splice(this.data.indexOf(item), 1)
    });
  }

    // Gim gi cho tt c

   /** Modal edit all price */
   new_discount: any = 0;
   isVisibleEditUnitPriceAll: boolean = false;

  /**
   * m modal nhp gi gim cho tt c
   */
   openEditUnitPriceAll() {
     this.isVisibleEditUnitPriceAll = true;
  
     this.vhAlgorithm.waitingStack().then(() => {
       this.new_discount = this.vhAlgorithm.vhnumeral('.unit_price_all');
     });
     
   }
 
  /**
   * x l sk nhn ok modal nhp gi gim cho tt c
   */
   handleEditUnitAll() {
     if (this.new_discount.getRawValue() >= 0
     ) {
       for (let i = 0; i < this.data.length; i++) {
         this.data[i].discount = parseFloat(
           document.getElementById('unit_price_all')['value']
         );
         this.data[i].discount = parseFloat(this.new_discount.getRawValue());
         this.discount_price_all = parseFloat(this.new_discount.getRawValue())
       }
     } else if (this.new_discount.getRawValue() < 0)
       this.vhComponent.showToast(
         2000,
         this.lang.translate('Invalid data, please check again'),
         'alert-toast'
       );
     this.isVisibleEditUnitPriceAll = false;
   }
 
  /**
   * ng modal nhp gi gim cho tt c v ko lu
   */
   handleCancelEditUnitAll() {
     this.isVisibleEditUnitPriceAll = false;
   }

}
