export interface VhPromotion {
    name: string;
    date_start: string;
    date_end: string;
    status: boolean;
    type: 1 | 2; // 1: Gim gi tin, 2: Gim gi %
    _id?: string;
}
export interface VhPromotionDetail {
    id_product: string;
    _id?: string;
    id_promotions?: string;
    discount: number;
    new_price: number;
    price?: number;
    img?: string;
    name?: string
}
export class VhPromotions {
    private date: Date = new Date();
    private promotions: VhPromotion = {
        name: '',
        date_start: this.date.toISOString(),
        date_end: this.date.toISOString(),
        status: true,
        type: 1, // 1: Gim gi tin, 2: Gim gi %
        _id: null
    }
    private promotionsDetail: VhPromotionDetail[] = []

    constructor(promotion: any = null, promotionDetail: any[] = null) {
        if (promotion) {
            this.promotions['date_start'] = promotion['date_start'] ? promotion['date_start'] : this.date.toISOString()
            this.promotions['date_end'] = promotion['date_end'] ? promotion['date_end'] : this.date.toISOString()
            this.promotions['status'] = promotion['status'] ? promotion['status'] : true
            this.promotions['type'] = promotion['type'] ? promotion['type'] : 1
            this.promotions['name'] = promotion['name'] ? promotion['name'] : ''
            this.promotions['_id'] = promotion['_id'] ? promotion['_id'] : null
        }
        if (promotionDetail) {
            this.promotionsDetail = promotionDetail
        }
    }

    /************************************X l *******************************************/
    public getName(): string {
        return this.promotions.name;
    }
    public setName(value: string) {
        this.promotions.name = value
    }
    public getType(): 1 | 2 {
        return this.promotions.type;
    }
    public setType(value: 1 | 2) {
        this.promotions.type = value
    }
    public getDateStart(): string {
        return this.promotions.date_start;
    }
    public setDateStart(value: string | Date) {
        this.promotions.date_start = typeof value == 'string' ? value : value.toISOString()
    }
    public getDateEnd(): string {
        return this.promotions.date_end;
    }
    public setDateEnd(value: string | Date) {
        this.promotions.date_end = typeof value == 'string' ? value : value.toISOString()
    }
    public getID(): string {
        return this.promotions._id
    }

    public addPromotionsDetail(product): boolean {
        let item = this.promotionsDetail.find(element => element.id_product == product._id)
        if (item) return false
        else {
            this.promotionsDetail.push({
                name: product.name,
                id_product: product._id,
                discount: 0,
                price: product.price,
                new_price: product.price,
                img: product.img
            })
            return true
        }
    }
    public removePromotionsDetail(id) {
        this.promotionsDetail.splice(this.promotionsDetail.findIndex(detail => detail['id_product'] == id), 1);
    }

    public getPromotionsDetail(): VhPromotionDetail[] {
        return this.promotionsDetail
    }
    /************************************X l *******************************************/

    /******************************create - update - delete invoice / detail *******************************/
    public getCreateUpdatePromotions() {
        return this.promotions
    }
    public getCreateUpdatePromotionsDetail(): VhPromotionDetail[] {
        return this.promotionsDetail.map(item => {
            return { id_product: item.id_product, new_price: item.new_price, discount: item.discount }
        })
    }
}