import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { PromotionsComponent } from './promotions.component';
import { AddComponent } from './add/add.component';
import { RouterModule, Routes } from '@angular/router';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { NgZorroAntModule } from 'src/app/ng-zorro-ant.module';
import { TranslateModule } from '@ngx-translate/core';
import { IonicModule } from '@ionic/angular';
import { SearchProductComponent } from './components/search-product/search-product.component';
import { DetailComponent } from './detail/detail.component';
import { ScrollingModule } from '@angular/cdk/scrolling';
import { CanLeavePage } from 'src/app/canLeavePage';
import { DiscountTakeGiftComponent } from './components/discount-take-gift/discount-take-gift.component';
import { DiscountManyProductsComponent } from './components/discount-many-products/discount-many-products.component';
import { DiscountPriceTotalInvoiceComponent } from './components/discount-price-total-invoice/discount-price-total-invoice.component';
import { DiscountPriceEachProductComponent } from './components/discount-price-each-product/discount-price-each-product.component';
import { DiscountPercentTotalProductComponent } from './components/discount-percent-total-product/discount-percent-total-product.component';
import { DiscountPercentProductComponent } from './components/discount-percent-product/discount-percent-product.component';
import { DetailDiscountEachProductComponent } from './detail/detail-discount-each-product/detail-discount-each-product.component';
import { DetailDiscountManyProductComponent } from './detail/detail-discount-many-product/detail-discount-many-product.component';
import { DetailDiscountTakeGiftComponent } from './detail/detail-discount-take-gift/detail-discount-take-gift.component';
import { DiscountSamePriceComponent } from './components/discount-same-price/discount-same-price.component';
import { DetailDiscountSamePriceComponent } from './detail/detail-discount-same-price/detail-discount-same-price.component';
import { DetailDiscountPercentProductComponent } from './detail/detail-discount-percent-product/detail-discount-percent-product.component';
import { DetailDiscountPercentProductsComponent } from './detail/detail-discount-percent-products/detail-discount-percent-products.component';
import { DetailDiscountTotalInvoiceComponent } from './detail/detail-discount-total-invoice/detail-discount-total-invoice.component';
const routes: Routes = [
  {
    path: '',
    component: PromotionsComponent
  },
  {
    path: 'add',
    component: AddComponent,
    canDeactivate: [CanLeavePage]
  },
  {
    path: 'detail',
    component: DetailComponent
  },
]

@NgModule({
  declarations: [PromotionsComponent, AddComponent, SearchProductComponent, DetailComponent, DiscountTakeGiftComponent, DiscountManyProductsComponent, DiscountPriceTotalInvoiceComponent, DiscountPriceEachProductComponent, DiscountPercentTotalProductComponent, DiscountPercentProductComponent, DetailDiscountEachProductComponent, DetailDiscountManyProductComponent, DetailDiscountTakeGiftComponent, DiscountSamePriceComponent, DetailDiscountSamePriceComponent, DetailDiscountPercentProductComponent, DetailDiscountPercentProductsComponent, DetailDiscountTotalInvoiceComponent],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    FormsModule,
    ReactiveFormsModule,
    NgZorroAntModule,
    TranslateModule,
    IonicModule,
    ScrollingModule
  ]
})
export class PromotionsModule { }
