import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { VhAlgorithm, VhQuerySales } from 'ionic-vhframeworks';
import { NzModalService } from 'ng-zorro-antd/modal';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { VhPromotions } from 'src/app/sales/interface/vh-promotions-money-rate';
import { LanguageService } from 'src/app/sales/services/language.service';

@Component({
  selector: 'app-detail',
  templateUrl: './detail.component.html',
  styleUrls: ['./detail.component.scss']
})
export class DetailComponent implements OnInit {
  // public vhPromotion: VhPromotions = new VhPromotions();
  public startTimeBackup: string;
  public endTimeBackup: string;
  tableHeight: any;
  data: any = [];
  promotion: any = {};
  promotionType: 1 | 2 | 3 | 4 | 5 | 6 | 7 = 1;
  id_promotion: any;
  price_reduce:any;
  price_value:any
  gift_length: number;
  dataStore:any
  constructor(
    private router: Router,
    private vhComponent: VhComponent,
    private vhQuerySales: VhQuerySales,
    private lang: LanguageService,
    public vhAlgorithm: VhAlgorithm,
    private cdRef: ChangeDetectorRef,
    private modal: NzModalService
  ) {
      this.dataStore = this.router.getCurrentNavigation().extras.state
      this.promotion = {...this.dataStore.promotion};
      
      
      if(this.promotion.type == 6) {
        this.gift_length = this.promotion.products_gift.length;
      }
      if(this.promotion.type == 7) {
        this.gift_length = this.promotion.products_gift.length;
        this.initNumberral()
      }
      
      if(this.promotion.promotion_all_products) {
       if(this.promotion.type == 1  ) this.price_reduce = this.promotion.price_reduce
       if( this.promotion.type  == 3 ) this.price_reduce = this.promotion.price_same
        this.promotion.products = []
        this.initNumberral()
      }
  }

  ngOnInit(): void {
   }

   /**
    * xa KM khi DB
    * @param id 
    */
  deletePromotion(id) {
      this.vhComponent.alertConfirm('','',`${this.lang.translate('Delete')} ${'khuyn mi ny'} ?`,'OK',this.lang.translate('Cancel'))
      .then(() => {
          this.vhQuerySales.deletePromotion(id).then((bool)=>{
                if (bool) {
                  let data = {
                    check:true,
                    type : this.dataStore.type,
                    date : this.dataStore.date
                   }
                  this.vhComponent.showToast(3000, 'Xa khuyn mi thnh cng', "success-toast");
                  this.router.navigate(['/sales/dashboard/manage/sales-program/promotions'],{state:data})
                }
              }, error => {
                this.vhComponent.showToast(3000, 'Xa khng thnh cng', "alert-toast")
              })
        },
        () => {}
      );
  }
  /**
   * bt sk thit lp p dng cho tt c sp
   */
  setAllProduct(event){
    if(event) this.initNumberral()
  }
  /**
   * to cleavejs cho gi gim  x l
   */
  initNumberral(){
    this.vhAlgorithm.waitingStack().then(() => {
      if(this.promotion.type == 1 || this.promotion.type == 3 || this.promotion.type == 7) this.price_value = this.vhAlgorithm.vhnumeral(".price_discout");
    });
  }

  /**
   * Cp nht KM 
   * @param products 
   */
  updateData(products) {
    // clear d liu  lu DB
    let promotion: any;
    
    if(this.promotion.type == 6) {
      promotion = {
        name: this.promotion.name,
        date_start: this.promotion.date_start,
        date_end: this.promotion.date_end,
        type: this.promotion.type,
        status: this.promotion.status,
        promotions_maximum: this.promotion.promotions_maximum,
        products: products.dataBuy,
        products_gift: products.dataGift
      }
    } else if (this.promotion.type == 7) {
      promotion = {
        name: this.promotion.name,
        date_start: this.promotion.date_start,
        date_end: this.promotion.date_end,
        status: this.promotion.status,
        type: this.promotion.type,
        products_gift: products,
        promotions_maximum: this.promotion.promotions_maximum,
        total_bill_value: parseFloat(this.price_value.getRawValue()),
        variable_enable: this.promotion.variable_enable

      }
    } else if (this.promotion.type == 4){
      promotion= {
        name: this.promotion.name,
        date_start: this.promotion.date_start,
        date_end: this.promotion.date_end,
        status: this.promotion.status,
        type: this.promotion.type,
        products: products
      }
    } else if (this.promotion.type == 5){
      promotion= {
        name: this.promotion.name,
        date_start: this.promotion.date_start,
        date_end: this.promotion.date_end,
        status: this.promotion.status,
        type: this.promotion.type,
        products: products
      }
    } else if (this.promotion.type == 3){
      promotion= {
        name: this.promotion.name,
        date_start: this.promotion.date_start,
        date_end: this.promotion.date_end,
        status: this.promotion.status,
        type: this.promotion.type,
        promotion_all_products: this.promotion.promotion_all_products,
        products: products,
        price_same : this.price_reduce
      }
      if(this.promotion.promotion_all_products) promotion['quantity_promotions_all']  = this.promotion.quantity_promotions_all
    }
     else {
      promotion= {
        name: this.promotion.name,
        date_start: this.promotion.date_start,
        date_end: this.promotion.date_end,
        status: this.promotion.status,
        promotion_all_products: this.promotion.promotion_all_products,
        type: this.promotion.type,
        products: products
      }
      if(this.promotion.promotion_all_products) promotion['quantity_promotions_all']  = this.promotion.quantity_promotions_all
    }
    this.vhComponent.alertConfirm(this.lang.translate("Confirm"), "", this.lang.translate("Save") + "?", 'OK', this.lang.translate("Cancel"))
    .then(() => {
      this.vhComponent.showLoading("", "transparent-loading").then(() => {
      if (promotion.promotion_all_products) {
          if (this.promotion.name == '') {
            return  this.vhComponent.hideLoading(0).then(() => this.vhComponent.showToast(1500, `${this.lang.translate("Vui lng khng  trng tn khuyn mi !")}`, "alert-toast"))
            }  
            else {
              switch (promotion.type) {
                case 3: 
                case 1: {
                   if(!this.price_value.getRawValue()) {
                    return this.vhComponent.hideLoading(0).then(() => this.vhComponent.showToast(2000,"Vui lng nhp gi tr", "alert-toast"))
                  } else {
                    promotion = { ...promotion, price_reduce: parseFloat(this.price_value.getRawValue())}
                    delete promotion.products
                    this.updatePromotion(promotion)
                    break;
                  }
                }
                case 2: {
                    let percent =  parseFloat(this.promotion.percent_reduce)
                    if(percent> 100 || percent < 0) {
                     return this.vhComponent.hideLoading(0).then(() => this.vhComponent.showToast(2000,"Gi tr % <= 100 hoc >= 0", "alert-toast"))
                    } 
                    else if(this.promotion.percent_reduce === undefined) {
                      return this.vhComponent.hideLoading(0).then(() => this.vhComponent.showToast(2000,"Vui lng nhp gi tr %", "alert-toast"))
                    }
                    else {
                        promotion = { ...promotion, percent_reduce: percent}
                        delete promotion.products
                        this.updatePromotion(promotion)
                        break;
                      }
                }                
              }
            }
          }
      else {
          if (this.promotion.name == '') {
            return  this.vhComponent.hideLoading(0).then(() => this.vhComponent.showToast(1500, `${this.lang.translate("Vui lng khng  trng tn khuyn mi !")}`, "alert-toast"))
          } else if(promotion.type !== 7 && promotion.products.length == 0) return this.vhComponent.hideLoading(0).then(() => this.vhComponent.showToast(1500, `${this.lang.translate("Vui lng chn sn phm khuyn mi !")}`, "alert-toast"))
          else if(promotion.type == 7 && promotion.products_gift.length == 0)  return this.vhComponent.hideLoading(0).then(() => this.vhComponent.showToast(1500, `${this.lang.translate("Vui lng chn sn phm khuyn mi !")}`, "alert-toast"))
          else {
           this.updatePromotion(promotion)
          }}
      })
    }, () => {})
  }

  /**
   * cp nht km ln DB sau khi  clear chun data
   * @param promotion 
   */
  updatePromotion(promotion){
    this.vhQuerySales.updatePromotion(this.promotion._id, promotion)
          .then((promt)=>{
                if (promt) {
                  let data = {
                    check:true,
                    type : this.dataStore.type,
                    date : this.dataStore.date
                   }
                  this.router.navigate(['/sales/dashboard/manage/sales-program/promotions'],{state:data})
                  this.vhComponent.hideLoading(0)
                }
              },error=>{
                
                this.vhComponent.showToast(2000, 'Cp nht khng thnh cng', "alert-toast");
                this.vhComponent.hideLoading(0)
            });
  }
  


  /**
   * bt sk thay i ngy bt u KM
   */
  setDateStart(value) {
    if (Date.parse(value) - Date.parse(this.promotion.date_end) > 0)
      this.vhComponent.alertMessage("", "", this.lang.translate("Staring date must be less than or the same as ending date"), "OK")
        .then(() => {
          this.startTimeBackup = this.promotion.date_start
        })
    else this.promotion.date_start = value
    
  }
  /**
   * bt sk thay i ngy ht hn KM
   */
  setDateEnd(value) {
    if (Date.parse(value) - Date.parse(this.promotion.date_start) < 0)
      this.vhComponent.alertMessage("", "", this.lang.translate("Staring date must be less than or the same as ending date"), "OK")
        .then(() => {
          this.endTimeBackup = this.promotion.date_end
        })
    else this.promotion.date_end = value    
  }
  /**
   * bt sk thay i loi KM
   */
  change(value) { 
    this.promotionType = value;
  }

  // isExit: boolean = false;
  goBack() {
    // this.isExit = true
    // this.vhComponent.alertConfirm(this.lang.translate("Confirm"), "", this.lang.translate("Are you sure to cancel the promotions"), "OK", (this.lang.translate("Cancel"))).then(() => { this.router.navigate(["/sales/dashboard/manage/sales-program/promotions"]) }).catch(() => { });
    this.router.navigate(["/sales/dashboard/manage/sales-program/promotions"], {state:this.dataStore})
  }

  /**
   * tr v tn loi chng trnh khuyn mi theo type ca KM
   * @param type 
   * @returns string
   * @example let type = this.renderType(1)
   */
  public renderType(type) {
    switch (type) {
      case 1: return 'Gim gi tin'
      case 2: return 'Gim gi phn trm'
      case 3: return 'Sn phm ng gi'
      case 4: return 'Gim gi tin khi mua nhiu sn phm'
      case 5: return 'Gim phn trm khi mua nhiu sn phm'
      case 6: return 'Tng qu khi mua nhiu sn phm'
      case 7: return 'Tng qu theo tng gi tr n hng'
    }
  }

  giftLength(e?) {
    this.gift_length = e;
    this.promotion.promotions_maximum = e
    
  }

  checkMaximum(){
    if(this.promotion.promotions_maximum == null || this.promotion.promotions_maximum > this.gift_length) this.promotion.promotions_maximum = this.gift_length
  }

}
